import React from 'react'
import './About.scss'
function About() {

  const navigateTo = (location) => {



    var height = document.getElementsByClassName("header")[0].offsetHeight;

    var container = document.getElementsByClassName('App')[0]
    var offsetTop;

    switch (location) {


      case "roadmap":
        offsetTop = document.getElementsByClassName("roadmap")[0].offsetTop - height;
        container.scroll({ top: offsetTop, left: 0, behavior: "smooth" });
        break;

      default:
        break;
    }

  }
  const openLink = (link) => {
    window.open(link)
  }
  return (
    <div className='about'>
      <span className='tc-gold fs-l ffam-regular'>ABOUT</span>

      <div className='about-inner'>
        <p className='tc-white ffam-light fs-s'>

          By adopting a Moggie from the collection, you will become a member of the Moggie Rescue Club. You wont just own a purrific NFT, but also contribute to helping selected cat related charities around the world, and the development of our most ambitious project : The Moggie Rescue Club Sanctuary.
          <br />
          <br />
          Were early crypto adopters and NFT enthusiasts ever since this technology existed. By opening the Moggie Rescue Club, we wanted to use NFTs to do good around us. We want to give stray cats the life they deserve in a safe and secured place, while making it a community driven project and showing the world that NFTs can be used for charitable purposes, in a simple way.
          <br />
          <br />
          The Club isnt about being a hyped project, thats why theres no limit regarding the mint per wallet count. All the profits from the mint, as well as the royalties, will be transparently used in order to help charitable organizations related to cats, and to build and run the Moggie Rescue Club Sanctuary IRL.
          <br />
          <br />
          The doors of the Moggie Rescue Club are open. There are a total of 10K Moggies available for adoption.
          <br />
          <br />
          Were taking care of the cats, but were not forgetting about our Club members. They will have exclusive access to content related to the project and the sanctuary, such as live cams, where they will be able to watch the life of our Sanctuary live and direct, high quality merch, a voting system for decisions, and access to other perks that weve planned as the project will grow.
    
          <br />
          <br />
          For more details about our plans and the genesis of our project, make sure to check our <a className='tc-gold' onClick={() => { navigateTo('roadmap') }}>roadmap</a> and our <a className='tc-gold' onClick={() => { openLink('https://moggierescueclub.medium.com/') }}>Medium</a>.
        </p>
      </div>
    </div>
  )
}

export default About